* {
  box-sizing: border-box;
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: "Poppins", sans-serif;
}
a {
  color: #a09ea7;
  text-decoration: none;
  text-decoration: underline;
}

a:hover {
  text-decoration: none;
  cursor: pointer;
}
.Header {
  height: 100px;
  background-color: #f55a5a;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
}
.Header i {
  font-size: 30px;
  margin: 5px;
}
.Card-container {
  padding: 50px;
}
.Card {
  display: flex;
}
.NonImageItemsToFlex {
  display: flex;
  flex-direction: column;
  padding: 20px;
}
.FlexForLocation {
  display: flex;
  gap: 10px;
  padding: 15px;
}
.placeImage {
  height: 400px;
  width: 267px;
  border-radius: 25px;
}
.placeName {
  letter-spacing: 2px;
}
.placeTitle {
  font-size: 45px;
  padding-bottom: 25px;
}
.Date {
  font-weight: bold;
  padding-bottom: 10px;
}
.Description {
  margin-right: 270px;
  font-size: 20px;
}
.verticalLine::after {
  content: "";
  height: 1500px;
  width: 200px;
  background-color: #d7e5ff59;
  position: absolute;
  top: 100px;
  right: 0;
}
